header {
  position: relative;
  display: block;
  width: 100%;
  background: black;
  color: white;
  height: 90px;
}
.logo {
  position: absolute;
  top: 50%; left: 15px;
  transform: translateY(-50%);
}
nav {
  position: absolute;
  top: 50%; right: 15px;
  transform: translateY(-50%);
}
nav, nav ul, nav ul li {
  margin: 0;
  padding: 0;
}
nav ul li {
  list-style-type: none;
  display: inline-block;
  margin-right: 10px;
}
.lastmenu {
  margin: 0;
}
nav ul li a {
  font-size: 19px;
  line-height: normal;
  color: white;
}
a {
  text-decoration: none;
  transition-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
  cursor: pointer;
  color: white;
}
a:hover, a:active, a:focus{
  text-decoration: none;
  color: red;
  transition-timing-function: cubic-bezier(0.445, 0.05, 0.55, 0.95);
}
footer {
  background-color: black;
  color: white;
}
.app-footer {
  position: fixed;
  bottom:0;
  width:100%;
  height:"20px";
}
.stocktable, .stockth, .stocktd {
  background-color: turquoise;
  border: 1px solid black  
}
.chart1, .chart3 {
  background-color: white;
  border: 1px solid black  
}
.chart2 {
  background-color: white;
  border: 1px solid black  
}

.cryptoSelection {
  background-color: white;
  text-align: center;
  margin: auto;
  width: 50%;
  border: 0px;
}
.cryptoSymName {
  padding-top: 20px;  
}
.cryptoDate {
  background-color: white;
  text-align: center;
  margin: auto;
  width: 50%;
  border: 0px;
}
.toCryptoDate {
  padding-left: 20px;
  background-color: white;
  height: 30px;
  width: 2px;
  display: inline-block;
}
.fromCryptoDate {
  background-color: white;
  height: 10px;
  width: 10px;
}
textarea {
  overflow-y: scroll;
  height: 100px;
  resize: none; /* Remove this if you want the user to resize the textarea */
}
.quoteTitle {
  color: "black";
  font-weight: 600;
  font-size: 14px;
  font-family: Arial, Verdana, sans-serif; color: #111111;
}
